
:root {
    --primary: #5e3bee;
    --heading-color: #282938;
    --bg-shade: #f5fcff;
    --github: #e62872;
    --darkblue: #1c1e53;
    --black: #000000;
    --white: #ffffff;
    --menu_height : 100px ;
    --my_font_size : 36px ;
  }


.myNav {
    display: flex;
    /* justify-content: space-around; */
    justify-content: space-between;
    align-items: center;
    /* background가 없으면 스크롤시 아래그림들이 메뉴를 덮어버린다. */
    background: white;
    opacity: 1;
    /* box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1); */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: var(--menu_height); 
    box-sizing : border-box ;
    font-size : medium ;
    /* z-index도 있어야 스크롤시 아래그림들이 뒷면으로 간다. */
    z-index: 50;
  }

  .my_com_logo{
    width: 180px ;
    height : 70px ;
  }

      
/* 회사로고와 햄버거 버튼 고정부분 */
.myNav_log_ham{
    /* background-color: gray; */
  width: 20%;
  display : flex ;
  justify-content: space-between ;
  padding : 20px 20px ;
  z-index: 2000;
  align-items: center;
}

.my_companyName{
  font-size: 1.8rem; 
  z-index: 2000;
}

.my_Mobile_companyName{
    display : none ;
}

my_Desk_Menu {	
    position : absolute ;
    /* position : fixed ; */
    top : 0 ;
    left : 0 ;
    width : 100% ;
    height: 100px; 
    overflow:hidden;    
    z-index : 1000 ;  /* 이게없으면 마우스를 .myNav위치에 올려놓으면 메뉴내려오는 반응이 없다 */
    /* background-color: skyblue; */
}

my_Desk_Menu:hover{
    height : 350px ;  
    transition-duration:0.5s;  
    /* color : green ; */
    background-color: white;
    z-index : 1000 ; 
    opacity: 1;
}   

.my_ul{
    display:flex ;
    flex-wrap : nowrap ;
    /* margin : 0 350px ; */
    /* 메뉴컬럼간격 */
    gap: 4rem ;
    justify-content: center ;
    font-size: 18px ;
    /* transform : translate(10%, 50%) ; */
    white-space:nowrap ;
    cursor : pointer ;
    /* text-align : center ;
    margin : auto ;
    align-items : center ; */
    /* background-color: yellow; */
}


/* ------- li 간격에서 갭까지 포한된 영역을 
                선택하기 위해서 ul에 font-size :0 주고 
   li 에서 폰트크기를 준다  */
