  /* --------------------------------------- */ 

 ul li { 
    list-style:none;   
   }
  

 ul > li {
    line-height:100px; 
    position: relative;
  
 }

 ul > li ul{ 
    width:100%;  
    position: absolute;
    text-align: center ; /* li 내에 중앙정렬  */ 
   
   
}
    
ul > li ul li{
    white-space:nowrap;
    line-height:50px; 
   
    /* background-color: greenyellow;  */
   }  

.my_link {
    text-decoration : none ;
    color : rgb(5, 5, 97) ;
}

.my_link:hover {
    color : gray ;
}

.clicked {
    display : none ;
}

my_mobile {
    display : none ;
}
.myNav-ham{
    display : none ;
  }

  
.my_Mobile_ul {
    background-color: white;   
    position: fixed ;
    width: 50%;
    top : var(--menu_height) ;
    left : 0 ;
    bottom : 0px ;
    overflow-y: auto;
   
    transition-duration: 150ms;   
    
    display : none ;
}
.my_Mobile_ul.active {   
    left : -100% ;
}



