  --menu_height : 100px ;
  --my_font_size : 36px ;
}

.top_margin{
  margin-top : 80px ;
  margin-bottom : 40px ;
  margin-left : 60px ; 
  font-size: 60px;
  font-weight: 900;
}

.hero__slider {
  /* overflow: hidden; */
