
:root {
  --primary: #5e3bee;
  --heading-color: #282938;
  --bg-shade: #f5fcff;
  --github: #e62872;
  --darkblue: #1c1e53;
  --black: #000000;
  --white: #ffffff;
  --menu_height : 100px ;
  --my_font_size : 36px ;
}

.my_body3{
  width:98% ;
  /* margin-left : 10% ; */
  /* background-color: skyblue; */
  text-align: center ;
  /* margin : 0 100px ; */
  padding : 10px  0px ;
}
.card{
  border : 1px solid blue ;
  background-color: rgb(33, 25, 71);
  border-radius: 8px;
  overflow : hidden ;
  height : 420px ;
  color : white ;
  cursor : pointer ;
 
  /* margin : 0 auto ; */
  /* margin : 0 150px ; */
  /* padding : 150px 150px ; */
 
}
 
.card-top h1 {
  font-size : 0.9rem ;
  margin : 20px ;
}

.card > img {
  width : 90% ;
  height: 300px;
  object-fit: fill ;
  margin : 20px auto ;
  border-radius: 10px ;
}
        
.card-bottom {
  margin : 10px 20px ;
}

.category {
  position : relative ;
}

.category::before {
  content: '';
  background: rgb(255, 61, 61);
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  right: 0;
  height: 2px;
}

