
:root {
  --primary: #5e3bee;
  --heading-color: #282938;
  --bg-shade: #f5fcff;
  --github: #e62872;
  --darkblue: #1c1e53;
  --black: #000000;
  --white: #ffffff;
  --menu_height : 100px ;
  --my_font_size : 36px ;
}


.my_body_2{
  width : 100vw ;
  /* height : 100vw ; */
  background-color: rgb(241, 249, 252);
 
 }

.main_grid{
  display : grid ;
  grid-template-columns : repeat(2,1fr) ;
  /* grid-auto-rows : 500px ; */
  grid-auto-rows : minmax(500px,auto) ;
  padding : 10px 50px;
}

/* 
.left_img_out{
