.title_margin{
  padding: 80px;
  font-size: 48px;
  text-align: center;
}
.left_img{
   width : 90% ;
  height : 90% ;
  /* height : 600px ; */
  object-fit: fill ;
  background-color: white;
  border-radius : 1rem ;
  box-shadow : 0 2px 8px 2px rgba(158, 148, 177, 0.189) ;
  /* object-fit : contain ; fill, contain,cover,none ,scale-down */
}

.right_state{

  width : 100% ;
  /* border : 2px blue solid ; */
  align-items : center ;
  text-align: center ;
}



@media screen and (max-width:768px) {
  
  
.main_grid{
  display : grid ;
  grid-template-columns : repeat(1,1fr) ;
  /* grid-auto-rows : 500px ; */
  /* grid-auto-rows : minmax(500px,auto) ; */
  padding : 10px 50px;
}
 
}