
:root {
  --primary: #5e3bee;
  --heading-color: #282938;
  --bg-shade: #f5fcff;
  --github: #e62872;
  --darkblue: #1c1e53;
  --black: #000000;
  --white: #ffffff;
  --menu_height : 100px ;
  --my_font_size : 36px ;
}

.C31_top_margin{
  margin-top : 30px ;
  margin-bottom : 10px ;
  margin-left : 60px ; 
  font-size: 60px;
  font-weight: 900;
  color :rgb(2, 58, 21);
}

.C31_top_margin2{
  margin-left : 60px ; 
  font-size: 20px;
  font-weight: 300;
}

.C31_v-container{
  /* border : 8px  solid rgb(232 232 232 / 93%) ; */
  /* background-color: orange; */
  display : flex ;
  flex : 2 ;
  padding: 2rem 4rem;
}

.C31_v-left{
  flex : 1 ;
  /* background : blue ; */
  text-align: center;
}
.C31_v-right{
  flex : 1 ;
  padding: 20px 50px;
  width : 100% ;
  /* background : yellow ; */
} 
