/* 반응형에서는는 스마트폰 사이즈보다 작게해야한다. */
.C31_image-container {
   width : 30rem ;
  height : 35rem ;
  overflow : hidden ;
  border-radius : 15rem 15rem 0 0 ;
  border : 8px solid rgba(198, 242, 116, 0.802) ;
  /* width:100% ;
  height : 100% ; */
}



.C31_items {
  margin-top: 20px;
}
.C31_item {
  background-color: #fff;
  /* margin-top: 30px; */
  padding: 10px;
  transition: 0.5s;
}

.C31_flexSB {
  display: flex;
  justify-content: space-between;
}

.C31_img2 {
  width: 70px;
  height: 70px;
  /* background-color: orange; */
}

/* .img3 {
  width: 70px;
  height: 70px;
} */

.text{
  margin-left : 10px ;
}
.C31_item p {
  color: #999999;
}

.C31_item p:hover {
  color: #fff;
}

.C31_item h2 {
  font-size: 20px;
  margin-bottom: 15px;
}

 .C31_item:hover {
  background-color: #1eb2a6;
  /* background-color: yellow; */
  color: #fff;
  cursor: pointer;
  box-shadow: 0 5px 25px -2px rgb(0 0 0 / 6%);
}


@media screen and (max-width:1068px) {
   
  .C31_v-container{
    flex-direction: column ;
    /* flex : 1 ; */
  }
  }
      

@media screen and (max-width:768px) {
  /* 반응형에서는는 스마트폰 사이즈보다 작게해야한다. */
